}
main.college-costs[data-state_showschoolerrors=yes][data-state_incomeselected=required] [data-state-based-visibility=income-required] {
  display: block;
}
main.college-costs[data-state_programrate=outOfState] span[data-state-based-visibility=out-of-state] {
  display: inline;
}
main.college-costs[data-state_usingnetprice=no] [data-state-based-visibility=using-net-price] {
  display: block;
}
main.college-costs[data-state_usingnetprice=no] span[data-state-based-visibility=using-net-price] {
  display: inline;
}
main.college-costs[data-state_investigation=yes] [data-state-based-visibility=investigation] {
