main.college-costs[data-state_investigation=yes] li[data-state-based-visibility=investigation] {
  display: list-item;
}

.m-stepper ol {
  display: grid;
  list-style-type: none;
  padding: 0;
  grid-auto-flow: column;
  gap: 2px;
}
.m-stepper li {
  display: block;
  height: 0.5rem;
  background: linear-gradient(to left, var(--gray-20) 50%, var(--green) 50%);
  background-size: 200% 100%;
  background-position: right bottom;
  transition: background-position 0.3s;
}
.m-stepper li.active {
  background-position: left bottom;
}
.m-stepper .current-step {
  font-weight: bold;
  font-size: 20px;
  line-height: 1;
  color: var(--white);
  background-color: var(--green);
  padding: 0.6rem 1rem;
  border-radius: 50%;
}
.m-stepper .h4 {
  display: inline;
  margin-left: 1rem;
}

#a-tooltip {
  max-width: 50%;
  min-width: 300px;
  z-index: 9;
  color: var(--black);
  background-color: var(--gray-5);
  padding: 0.9375rem;
}

.a-tooltip {
  border: none;
  background: none;
}
.a-tooltip span {
  visibility: hidden;
  display: block;
}

